export const Modal =({
    isOpen,
    onClose,
    children
})=>{

    if(!isOpen) return null;
    return (
        <div>
            <div className="absolute inset-0 flex items-center justify-center z-50"> </div>
            <div className="bg-white rounded-lg p-4"> { children } </div>
        </div>
    )
}